
h3 {
    text-align: center;
    color:#eee;
    font-size:25px;
    margin-bottom:0px;
}

input {
    font-size:20px;
    border:0px;
    border-radius: 3px;
    padding:5px;
    background-color: #eee;
    width:50%;
}

textarea {
    font-size:20px;
    border:0px;
    border-radius: 3px;
    width: 80%;
    height: 70%;
    padding:5px;
    margin-bottom:15px;
    resize:none;
}

.container {
    width: 100%;
    height: 100vh;
    background-color: gray;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1 {
    font-size:50px;
    color:#eee;
}

.contentwrapper {
    width: 80%;
    height: 70%;
    border:1px solid #eee;
    border-right:0px;
    border-radius: 10px;
    display:flex;
    justify-content: center;
    align-items: center;
}

.box {
    width: 50%;
    height: 100%;
    max-height: 700px;
}

.input {
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.inputwrapper {
    flex:1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.inputcontentwrapper {
    flex:2;
    width:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.tweets {
    overflow-y: scroll;
    overflow-x:hidden;
}

.tweets::-webkit-scrollbar {
    width: 15px;
    background-color: #eee;
    border-radius: 15px;
    padding:0px;
}

.tweets::-webkit-scrollbar-thumb {
    background-color: lightslategrey;
    border-radius: 15px;
    margin:0px;
}

.inputsubmit {
    flex:0.2;
    margin-bottom:15px;
}