.contentbox {
    height: 150px;
    display: flex;
    align-items: center;
}

.content {
    display:flex;
    flex-direction: column;
    align-items: left;
}

.contentbox img {
    margin-left:10px;
    margin-right:10px;
    width: 70px;
    height: 70px;
}

.contentbox h4 {
    font-size: 20px;
    color:black;
    margin-bottom:0px;
}

.contentbox p {
    color:#eee;
}
